import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [{
      path: '/login',
      name: 'login',
      component: () => import('@/views/Login.vue'),
    },
    {
      path: '/',
      name: '/home',
      component: () => import('@/views/Login.vue'),
      redirect: to => {
        return '/home'
      },
    },
    //-----------------------------------------------------------------------------
    {
      path: '/modules',
      component: () => import('@/views/Modules.vue'),
      children: [{
          // 首页
          path: '/home',
          name: '/home',
          component: () => import('@/views/shouye/index.vue')
        }, {
          // 通知公告
          path: '/notice',
          name: '/notice',
          component: () => import('@/views/xiaoxitongzhi/notice.vue'),
          redirect: to => {
            return '/notice/activeInfo'
          },
          //活动信息
          children: [{
              path: '/notice/activeInfo',
              name: '/notice',
              component: () => import('@/views/xiaoxitongzhi/activeInfo.vue')
            },
            {
              //会议信息
              path: '/notice/meetInfo',
              name: '/notice',
              component: () => import('@/views/xiaoxitongzhi/meetInfo.vue')
            }
          ]
        },
        //党务管理
        {
          path: '/activities',
          name: '/activities',
          redirect: to => {
            return '/activities/centgroup'
          },
          component: () => import('@/views/dangwuguanli/activities.vue'),
          children: [
            // 中心组工作
            {
              path: '/activities/centgroup',
              name: '/activities',
              component: () => import('@/views/dangwuguanli/centgroup.vue'),
            },
            //党组织管理
            {
              path: '/activities/organization',
              name: '/activities',
              component: () => import('@/views/dangwuguanli/organization.vue'),
            },
            //党员信息
            {
              path: '/activities/information',
              name: '/activities',
              component: () => import('@/views/dangwuguanli/information.vue'),
            },
            //审核提交
            {
              path: '/activities/examine',
              name: '/activities',
              component: () => import('@/views/dangwuguanli/examine.vue'),
            },
            //优秀党员
            {
              path: '/activities/excellent',
              name: '/activities',
              component: () => import('@/views/dangwuguanli/excellent.vue'),
            },
            //党建朱锋
            {
              path: '/activities/vanguard',
              name: '/activities',
              component: () => import('@/views/dangwuguanli/vanguard.vue'),
            },
            //党务公开
            {
              path: '/activities/public',
              name: '/activities',
              component: () => import('@/views/dangwuguanli/public.vue'),
            },
            //特色党建
            {
              path: '/activities/characteristic',
              name: '/activities',
              component: () => import('@/views/dangwuguanli/characteristic.vue'),
            }
          ]
        }, {
          //组织生活
          path: '/organizational',
          name: '/organizational',
          redirect: to => {
            return '/organizational/active'
          },
          component: () => import('@/views/zuzhishenghuo/organizational.vue'),
          children: [{
              path: '/organizational/active',
              name: '/organizational',
              component: () => import('@/views/zuzhishenghuo/active.vue'),
            },
            {
              path: '/organizational/meet',
              name: '/organizational',
              component: () => import('@/views/zuzhishenghuo/meet.vue'),
            }
          ]
        }, {
          //积分管理
          path: '/integral',
          name: '/integral',
          redirect: to => {
            return '/integral/integralindex'
          },
          component: () => import('@/views/jifenguanli/integral.vue'),
          //积分管理
          children: [{
            path: '/integral/integralindex',
            name: '/integral',
            component: () => import('@/views/jifenguanli/integralindex.vue'),

          }]
        }, {
          //学习系统
          path: '/study',
          name: '/study',
          redirect: to => {
            return '/study/examination'
          },
          component: () => import('@/views/xuexiguanli/study.vue'),
          //考试管理
          children: [{
              path: '/study/examination',
              name: '/study',
              component: () => import('@/views/xuexiguanli/examination.vue'),
            },
            //课程管理
            {
              path: '/study/class',
              name: '/study',
              component: () => import('@/views/xuexiguanli/class.vue'),
            },
            //党员学习笔记
            {
              path: '/study/note',
              name: '/study',
              component: () => import('@/views/xuexiguanli/note.vue'),
            },
            //规章制度
            {
              path: '/study/regulations',
              name: '/study',
              component: () => import('@/views/xuexiguanli/regulations.vue'),
            }
          ]
        }, {
          //模板管理
          path: '/template',
          name: '/template',
          redirect: to => {
            return '/template/templateindex'
          },
          component: () => import('@/views/mobanguanli/template.vue'),
          //模板管理
          children: [{
            path: '/template/templateindex',
            name: '/template',
            component: () => import('@/views/mobanguanli/templateindex.vue')
          }]
        }, {
          //大数据 
          path: '/bigdata',
          name: '/bigdata',
          component: () => import('@/views/dashuju/bigdata.vue')
        },
        {
          //系统设置
          path: '/setting',
          name: '/setting',
          redirect: to => {
            return '/setting/dictType'
          },
          component: () => import('@/views/setting/setting.vue'),
          //字典设置
          children: [{
            path: '/setting/dictType',
            name: '/setting',
            component: () => import('@/views/setting/dictType.vue')
          }, {
            //系统日志
            path: '/setting/sysLog',
            name: '/setting',
            component: () => import('@/views/setting/sysLog.vue')
          }]
        }
      ]
    }
  ]
})



